@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

* {
	margin: 0;
	padding: 0;

}

body {
	font-family: 'Roboto', sans-serif;
	background: #666;
	height: 100%;
}

.container {
	width: 800px;
	margin: 0 auto;
	padding-top: 5rem;
}

.card {
	padding: 1rem;
	border-radius: 6px;
	background: #fff;
	box-shadow: 2px 3px 10px rgba(0, 0, 0, .2);
}

.btn {
	font-weight: 700;
	position: relative;
	margin-right: 1rem;
	padding: 0.5rem 1.5rem;
	cursor: pointer;
	transition: all 0.22s;
	text-decoration: none;
	text-transform: uppercase;
	color: #42b983;
	border: 1px solid #42b983;
	border-radius: 99px;
	outline: none;
	background: #fff;
}

.btn.primary {
	color: #fff;
	background: #42b983;
}

.btn:active {
	box-shadow: inset 1px 1px 1px rgba(0, 0, 0, 0.3);
}

.btn:hover {
	cursor: pointer;
	opacity: 0.8;
}

input[type=file] {
	display: none;
}

.preview {
	display: flex;
	flex-wrap: wrap;
	padding: .5rem;
}

.preview-image {
	position: relative;
	overflow: hidden;
	margin-right: .5rem;
	margin-bottom: .5rem;
}

.preview-image.removing {
	transition: transform .3s;
	transform: scale(0);
}

.preview-image img {
	width: 180px;
	height: auto;
}

.preview-image:hover .preview-remove {
	opacity: 1;
}

.preview-image:hover .preview-info {
	bottom: 4px;
}

.preview-remove {
	font-weight: bold;
	position: absolute;
	top: 0;
	right: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 20px;
	height: 20px;
	cursor: pointer;
	transition: opacity .22s;
	opacity: 0;
	background: rgba(255, 255, 255, .5);
}

.preview-info {
	font-size: .8rem;
	position: absolute;
	right: 0;
	bottom: -30px;
	left: 0;
	display: flex;
	align-items: center;
	justify-content: space-between;
	height: 24px;
	padding: 0 5px;
	transition: bottom .22s;
	background: rgba(255, 255, 255, .5);
}

.preview-info-progress {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: width .22s;
	background: #42b983;
}
